.imgContainer{
    position: fixed;
    width: 100%;
    height: 100vh;
    background: rgb(221, 42, 42);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
}
.imgContainer .bg{
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: absolute;
    height: 100%;
    width: 100%;
    opacity: 0;
    background-size: cover;
    background-repeat: no-repeat;
    animation: camera 30s infinite;
}
.imgContainer .imgLink{
    position: absolute;
    bottom: 15%;
    left: 10%;
}
.imgContainer .imgLink span{
    color: #fff;
    padding: 10px 20px;
    cursor: pointer;
    transition: all .3s ease-in-out;
}
.imgContainer .imgLink span:hover{
    background-color: rgb(27, 27, 27);
    padding: 10px 30px;
}

@keyframes camera{
    0%{
        background-position: 0% 0%;
    }
    25%{
        background-position: 40% 10%;
    }
    50%{
        background-position: 0% 10%;
    }
    75%{
        background-position: 10% 40%;
    }
    100%{
        background-position: 0% 0%;
    }
}